* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
  list-style-position: inside;
}

p {
  line-height: 25px;
}

main {
  display: flex;
  flex-flow: column;
  gap: 15px;
  align-items: center;
  justify-content: space-between;
}

main .parent {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  grid-gap: 20px;
}

body {
  background: #444;
  color: #f1f1f1;
  font-family: Arial;
}

#root {
  padding: 30px;
}

#root h1 {
  margin-bottom: 15px;
}

#root h3 {
  margin-bottom: 15px;
}

#root p {
  font-style: italic;
  margin-bottom: 20px;
  max-width: 600px;
}